@import url("https://fonts.googleapis.com/css2?family=Oswald&display=swap");

body {
  position: relative;
  background-color: black;
  font-family: "Oswald", sans-serif;
  background: url("wallpaper2.gif");
  background-size: 100%;
  background-position-y: 39%;
  background-attachment: fixed;
  margin: 0px;
}

.App {
  text-align: none;
  overflow: hidden;
  user-select: none;
  height: 100%;
}

html {
  scroll-behavior: smooth;
}

.navbar {
  font-family: 'Lobster', cursive;
  position: fixed;
  height: 8vh;
  width: 100%;
  background-color: rgba(0, 0, 0, 0);
  z-index: 200;
  transition: 0.5s;
  filter: opacity(0.5);
}

.navbar:hover {
  background-color: rgba(0, 0, 0, 1);
  filter: opacity(1);
}

.navbar-items {
  width: 50%;
  height: 100%;
  display: flex;
  margin: auto;
  justify-content: space-between;
}

.navbar-item {
  position: relative;
  top: 50%;
  transform: translateY(-50%);
  font-size: 3vmin;
  color: rgb(255, 255, 255);
}

.navbar-item:hover {
  color: rgb(182, 182, 182);
  cursor: pointer;
}

.wallpaper {
  width: 100%;
  height: 100vh;
  display: flex;
  position: absolute;
  transition: 1s;
}

.scroll:hover {
  filter: opacity(1);
}

#container1 {
  position: relative;
  height: 100vh;
  width: 100%;
  background: url("wallpaper1.gif");
}

#container2 {
  position: relative;
  height: fit-content;
  padding-top: 15vmin;
  padding-bottom: 15vmin;
  transition: 1s;
  overflow: hidden;
}

#container3 {
  height: 100vh;
  padding-bottom: 10vmin;
  transition: 1s;
}

#container4 {
  position: relative;
  height: 100vh;
  padding-bottom: 10vmin;
  background-size: cover;
  background-position-y: 70%;
  background-color: rgba(182, 182, 182, 0);
}

#container5 {
  position: relative;
  height: fit-content;
}

#container6 {
  position: relative;
  height: auto;
  padding-top: 10vmin;
  padding-bottom: 10vmin;
}


.bottom-bar {
  position: relative;
  bottom: 0;
  height: fit-content;
  background-color: rgb(0, 0, 0);
  padding-bottom: 2vmin;
  z-index: 1000;
}

.text-wrapper {
  position: relative;
  top: 28vh;
  margin: auto;
  width: 44%;
  z-index: 2;
  transition: 0.2s;
  text-align: center;
  color: rgb(255, 255, 255);
}

@import url('https://fonts.googleapis.com/css2?family=Lobster&display=swap');

.large {
  position: relative;
  font-size: 10vmin;
  font-family: 'Lobster', cursive;
  margin-top: 3vmin;
  margin-bottom: 3vmin;
  top: 8vmin;
  color: rgb(255, 255, 255);
  border-radius: 3vmin;
  text-shadow: 0px 0px 15px rgb(0, 225, 255), 0px 0px 35px rgb(0, 225, 255);
  animation: 2s;
  transition: 2s;
  cursor: pointer;
  transform: translate(20%);

}

.medium {
  position: relative;
  font-family: 'Lobster', cursive;
  font-size: 5vmin;
  font-weight: 100;
  letter-spacing: 0.1vw;
  margin: 0;
  padding: 0;
  top: 8vmin;
  filter: opacity(1);
  z-index: 100;
  transition: 2s;
  transform: translate(-20%);

}

.small {
  position: relative;
  font-weight: 400;
  font-size: 3vmin;
  letter-spacing: 0.1vmin;
  top: 8vmin;
  filter: opacity(1);
  z-index: 100;
  transition: 2s;
  transform: translate(-10%);

}


.slide-header {
  margin: 0;
  font-size: 3.5vmin;
  font-weight: 400;
  margin-left: 2vmin;
  color: rgb(0, 0, 0);
}

.slide-footer {
  margin: 0;
  font-size: 2vmin;
  font-weight: 100;
  margin-left: 2vmin;
  color: rgb(0, 0, 0);
}

a:hover,
a:visited,
a:link,
a:active {
  text-decoration: none;
}

.header {
  font-family: 'Lobster', cursive;
  position: relative;
  font-size: 6vmin;
  color: rgb(255, 255, 255);
  text-shadow: 1px 1px 1px rgb(0, 0, 0);
  font-weight: 500;
  text-align: center;
  margin: 0;
  margin-left: auto;
  margin-right: auto;
  padding: 0;
  padding-top: 12vmin;
  margin-bottom: 5vmin;
  z-index: 100;
}

.header-medium {
  font-size: 4vmin;
}

.black {
  color: black;
}

.bottom-bar-text {
  color: white;
  position: relative;
  text-align: center;
  font-size: 1vmax;
  font-weight: 400;
  letter-spacing: 0.2vmin;
}

.project-wrapper {
  position: relative;
  width: 45%;
  margin-top: 5%;
  margin-left: 10%;
  transition: 1s;
  border-radius: 2vmin;
  filter: drop-shadow(0px 0px 10px rgb(132, 0, 255)) drop-shadow(0px 0px 30px rgb(132, 0, 255)) drop-shadow(0px 0px 80px rgb(132, 0, 255));
  transform:translateY(50%);
  transition: 1s;
}

.service-wrapper {
  position: relative;
  display: flex;
  flex-direction: row;
  flex-wrap: nowrap;
  justify-content: space-between;
  width: 70%;
  left: 15%;
  margin-top: 5vh;
  padding: 3vmin;
  padding-bottom: 8vmin;
  padding-top: 8vmin;
  transition: 1s;
}

.service {
  width: 30%;
  padding-top: 2%;
  padding-bottom: 2%;
  display: inline-block;
  color: rgb(255, 255, 255);
  text-shadow: 1px 1px 1px rgb(0, 0, 0);
  border-radius: 10%;
  background-color: rgba(255, 0, 221, 0.15);
  border: 2px solid rgb(255, 0, 221);
  transition: 0.5s;
  animation: 10s service-color linear infinite;
  box-shadow: 0px 0px 150px rgb(255, 0, 221);
}

.service:nth-child(2n){
 animation-delay: 1s;
}

.service:nth-child(3n){
 animation-delay: 2s;
}

@keyframes service-color{
  0%{filter:hue-rotate(0deg)}
  100%{filter: hue-rotate(360deg)}
}


.service-title {
  position: relative;
  margin: auto;
  width: 75%;
}

.service-description {
  position: relative;
  text-align: center;
  font-size: 2.6vmin;
  width: 80%;
  margin: auto;
  margin-top: 3vw;
  background-repeat: no-repeat;
  background-position: top;
  filter: drop-shadow(0px 0px 30px rgb(255, 255, 255));
  transition: 0.5s;
  border-radius: 1vmin;
}

.skills{
  transition: 1s;
}

.skill-wrapper {
  position: relative;
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  justify-content: center;
  width: 80%;
  margin: auto;
  padding: 5vmin;
  border-radius: 3vmin;
  transition: 1s;
}

.skill {
  position: relative;
  display: inline-grid;
  width: 9vmin;
  height: 9vmin;
  text-align: center;
  font-weight: 900;
  color: rgb(0, 0, 0);
  margin: 1vmin;
  overflow: hidden;
  background-color: rgba(0, 247, 255, 0.1);
  filter: drop-shadow(0px 0px 10px rgb(0, 206, 233));
  animation: 1s;
  border-radius: 100vmin;
}

.skill:nth-child(2n){
  transform: translateY(20%);
}

.skill:hover {
  background-color: rgba(0, 247, 255, 0.2);
}

.tech-icon {
  width: 100%;
  display: block;
  margin: auto;
}

.service-icon {
  width: 60%;
  display: block;
  margin: auto;
}

.skill-description {
  position: relative;
  top: 0%;
  left: 5%;
  width: 90%;
  font-size: 1vmax;
  font-weight: 400;
  letter-spacing: 0;
  font-size: 1.8vmin;
  vertical-align: top;
}

.skill-title {
  background: linear-gradient(to top, rgb(0, 255, 191), rgb(0, 255, 106));
  height: fit-content;
  line-height: 5vmin;
  color: rgb(0, 0, 0);
}

.contact-wrapper {
  width: 50%;
  margin: auto;
  padding: 5vmin;
  padding-top: 2vmin;
  padding-bottom: 2vmin;
  display: flex;
  border-radius: 3vmin;
}

.contact {
  display: block;
  margin: auto;
  margin-top: 3vmin;
  margin-bottom: 3vmin;
}

.contact:hover {
  filter: brightness(1.2);
}

.contact-icon {
  width: 30%;
  display: block;
  margin: auto;
  border-radius: 0%;
  background-color: black;
  border-radius: 2vmin;
}

.navbar-icon {
  display: none;
}

.scroll-container {
  position: fixed;
  display: flex;
  justify-content: center;
  width: 100%;
  top: 88%;
}

.scroll-button {
  width: 4vmin;
  margin-left: 1vmin;
  margin-right: 1vmin;
  transition: 0.3s;
}

.scroll-button:hover{
  transform: scale(1.3);
}

.carousel-control-next,
.carousel-control-prev {
  height: 20%;
  width: 10%;
  top: 80%;
  background-color: rgba(255, 255, 255, 0.2);
  transition: 0.3s;
}

.carousel-control-next:hover,
.carousel-control-prev:hover {
  background-color: rgba(255, 255, 255, 0.4);

}


.counter-container {
  position: relative;
  transition: 1s;
  filter: opacity(1);
}

.counter-icon {
  filter: drop-shadow(0px 0px 10px rgb(255, 0, 221))
    drop-shadow(0px 0px 10px rgb(255, 0, 221));
  margin-bottom: 3vmin;
  transition: 1s;
}

.carousel-inner{
  overflow: visible;
}

.carousel-caption {
  color: rgb(255, 255, 255) !important;
  background-color: black;
  border-radius: 3vmin;
}

.carousel-text{
  position: absolute;
  top: 0%;
  left: 102%;
  background-color: rgb(61, 61, 61);
  color: white!important;
  z-index: 100;
  width: 50vmin;
  height: fit-content;
  padding: 2vmin;
}

.carousel-link{
  color: rgb(203, 135, 248)!important;
}

.carousel-link:hover{
  color: rgb(244, 210, 255)!important;
}

.twitter{
  width: 80%;
  display: block;
  position: relative;
  border-radius: 3vmin;
  box-shadow: 5px 5px 20px rgba(0, 0, 0, 0.473);
}

@media only screen and (max-device-width: 600px), (max-width: 600px) {

  #container2{
  background-color: rgb(21, 54, 85);
  }

  #container6{
    background-color: rgb(21, 54, 85);
    }

    .twitter{
      width: 100%!important;
      top: 0%;
